import * as Phaser from 'phaser';

const BTN_MENU = {
	WIDTH:  80,
	HEIGHT: 25,
};

/**
 * Handle menu's button(s).
 */
class BtnMenu extends Phaser.Group {
	/**
	 * Display the menu next to the cursor.
	 * @constructor
	 * @param {Phaser} game - The Phaser instance.
	 */
	constructor(game) {
		super(game);
	}
}

export { BtnMenu as default, BTN_MENU };



